.frost-checkbox {
  display: inline-block;
  position: relative
}

.frost-checkbox input {
  display: none;
  left: 2px;
  outline: none;
  position: absolute;
  top: 2px;
}

.frost-checkbox label {
  color: #929497;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.frost-checkbox label::before {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#f0f2f4));
  background-image: linear-gradient(#fff, #f0f2f4);
  border: 1px solid #babcbe;
  content: '';
  display: inline-block;
  margin-right: 5px;
  position: relative;
  vertical-align: middle;
}

.frost-checkbox label:focus {
  outline: none;
}

.frost-checkbox label:focus::before {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(240, 250, 255)), to(rgb(214, 241, 255)));
  background-image: linear-gradient(rgb(240, 250, 255), rgb(214, 241, 255));
  border: 1px solid #929497;
}

.frost-checkbox svg {
  fill: #009eef;
  left: 0;
  position: absolute;
  top: 0;
  visibility: hidden;
}

.frost-checkbox-checked svg {
  visibility: visible;
}

.frost-checkbox-checked.frost-checkbox-disabled svg {
  fill: #babcbe;
}

.frost-checkbox-disabled label::before {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#f0f2f4), to(#f0f2f4));
  background-image: linear-gradient(#f0f2f4, #f0f2f4);
  border: 1px solid #e2e2e2;
}

.frost-checkbox-error label::before {
  border: 1px solid #d1625e;
}

.frost-checkbox-large label::before {
  height: 30px;
  width: 30px;
}

.frost-checkbox-large svg {
  height: 30px;
  width: 30px;
}

.frost-checkbox-medium label::before {
  height: 25px;
  width: 25px;
}

.frost-checkbox-medium svg {
  height: 25px;
  width: 25px;
}

.frost-checkbox-small label::before  {
  height: 20px;
  width: 20px;
}

.frost-checkbox-small svg {
  height: 20px;
  width: 20px;
}



